// Global SCSS imports
@import "vars";
@import "reset";
@import "global";
@import "fonts";
@import "mixin";
@import "extend";

// Sections SCSS files imports

.header{
  height: 100vh;
  width: 100%;
  &__inner{
    display: flex;
  }
  
  &__img{
    overflow: hidden;
    height: 100vh;
    img{
      height: 100%;
    }
  }

  &__content{
    padding: 68px 20px 61px 100px;
    display: flex;
    flex-direction: column;
  }

  &__title{
    margin-top: 90px;;
  }

  &__text{
    margin-bottom: 40px;
  }

  &__btn{
    padding:18px 30px;
    max-width: 250px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:hover{
      svg{
        transition: all .3s;
        transform: translateX(10px)
    }
    }
  }

  &__bottom{
    display: flex;
    margin-top: auto;

    &-text{
    font-weight: 375;
    font-size: 18px;
    line-height: 20px;
    color: $fsgray;
    }
    &-link{
      margin-left: 5px;
      font-weight: 494;
      font-size: 20px;
      line-height: 22px;
      color: $fsred;
      transition: all .3s;
      &:hover{
        color: $Hovbg;
      }
    }
  }
}

//---------------------------BOX---------

.steps__numbers{
  display: flex;
  align-items: center;
  position: relative;
}

.progress{
  position: absolute;
  max-width: 650px;
  top: 50%;
  right: 0;
  left: 0;
  height: 2px;
  background-color: #E5E4E1;
  z-index: -1;
  &__success{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 2px;
    background-color: $Bgred;
    transition: all .4s;
  }
}

.step__number{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border: 1px solid #B3B4BA;
  border-radius: 50%;
  background-color: #fff;
  & + .step__number{
    margin-left: 100px;
  }

  &--active{
    background-color: $Bgred;
    transition: all .8s;
      path{
        fill: #FFFFFF;
        transition: all .8s;
      }
  }
}

.steps{
  &__title{
    margin-top: 30px;
  }
  &__text{
    margin-bottom: 40px;
  }

  &__forms{
    display: none;

    &--active{
      display: flex;
      flex-direction: column;
    }
  }

  &__button{
    margin-top: 30px;
  }
}

.form__input{
  max-width: 460px;
  position: relative;
  &-search{
    width: 100%;
    padding: 20px 65px;
    font-weight: 375;
    font-size: 18px;
    line-height: 20px;
    max-width: 460px;
  }

  svg{
    position: absolute;
    top: 30%;
    left: 30px;
  }
}

//-------------------RADIO---------------

.radio__button{
  max-width: 1120px;
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 20px;
  &-label{
    max-width: 360px;
    display: flex;
    align-items: center;
    padding: 10px;
    background-color: $Bgblue;
    position: relative;
    cursor: pointer;
  }
  
  &-input{
    display: none;
    &:checked + .radio__label-face{
      border: 1px solid $Bgred;
      &::after{
        opacity: 1;
      }
    }

    &:checked ~ .radio__label-border{
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      border: 1px solid $Bgred;
    }
  }
}
  
.radio__label-face{
  width: 30px;
  height: 30px;
  border: 1px solid $Bggray;
  background-color: #fff;
  border-radius: 50%;
  margin-left: auto;
  position: absolute;
  right: 30px;
  &::after{
    content: "";
    position: absolute;
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    background-color: $Bgred;
    transform: translate(-50%,-50%);
    opacity: 0;
  }
}

  .radio__label-img{
    margin-right: 30px;
    padding: 10px;
    background-color: #fff;
  }

  //----------------CHECKBOX---------------------

.checkboxes__items{
  max-width: 1120px;
  display: grid;
  grid-template: repeat(4, auto)/ repeat(3, 1fr);
  gap: 20px;
}

.checkboxes__label{
  max-width: 360px;
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: $Bgblue;
  position: relative;
  cursor: pointer;

  &-input{
    display: none;
    &:checked ~ .checkboxes__label-face{
      border: none;
      &::after{
        opacity: 1;
        //также всі зміни можна записати сюда без opacity но тут я хочу шоб все було плавно з transition
      }
    }

    &:checked ~ .checkboxes__label-border{
      opacity: 1;
      //также всі зміни можна записати сюда без opacity но тут я хочу шоб все було плавно з transition
    }
  }

  
  &-img{
    padding: 10px;
    background-color: #fff;
    margin-right: 20px;
  }
  &-face{
    position: absolute;
    width: 30px;
    height: 30px;
    top: 25px;
    right: 25px;
    background: #FFFFFF;
    border: 1px solid #E5E4E1;
    border-radius: 5px;
    &::after{
      content: "";
      position: absolute;
      display: block;
      background: $Bgred url(../img/checkboxes/check.svg) center no-repeat;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 5px;
      transition: opacity .3s;
      opacity: 0;
    }
  }

  &-border{
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      border: 1px solid $Bgred;
      transition: opacity .3s;
      opacity: 0;
  }
  
}

//----------------FILE-------------

.file{
  &__uploader{
    display: flex;

    &-down{
      padding: 124px 105px;
      text-align: center;
      background-color: $Bgblue;
      border: 2px dashed $fsblue;
      cursor: pointer;
      position: relative;
    }
  }

  &__down-input{
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
  }

  &__down-text{
    margin-top: 30px;
  }

  &__uploader-item{
    margin-left: 40px;
    display: flex;
    flex-direction: column;
    flex-basis: 720px;

    .form__items-title{
      margin: 0 0 25px;
    }
  }

  &__item-link{
    display: flex;
    padding: 20px 30px;
    border: 1px solid $Bggray;
    max-width: 720px;
    transition: all .3s;

    & + .file__item-link{
      margin-top: 10px;
    }

    path{
      transition: all .5s;
    }

    &:hover{
      border: 1px solid $fsblue;
      path{
        fill: $fsblue;
      }
      .file__link-text{
        color: $fsblue;
      }
    }
  }

  &__link-text{
    margin-left: 15px;
    color: $fsgray;
    font-weight: 375;
    font-size: 18px;
    line-height: 20px;
    transition: all .3s;
  }
}

//-----------------------SELECT------------------

.select{

  &__body{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    max-width: 1150px;
    & + .select__body{
      margin-top: 30px;
    }
  }

  &__label{
    padding: 15px 24px;
    border: 1px solid $Bggray;
    font-size: 18px;
    line-height: 20px;
    color: $fsgray;
    cursor: pointer;
    position: relative;

    &-input{
      display: none;

      &:checked + .select__label-text{
        color: $fsblue;
      }

      &:checked ~ .select__label-border{
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        border: 1px solid $fsblue;
      }
    }
  }
}

//----------------------FORM--------------

.form__finish{
  max-width: 930px;
  display: flex;
  flex-direction: column;

  &-body{
    display: flex;
    width: 100%;
    margin-bottom: 10px;
  }

  &-label{
    width: 100%;
    border: 1px solid $Bggray;
    position: relative;
    
    & + .form__finish-label{
      margin-left: 10px;
    }

    &::after{
      content: '';
      position: absolute;
      display: block;
      top: 20px;
      left: 30px;
      bottom: 0;
      width: 20px;
      height: 20px;
      background: url(../img/form__finish.svg) no-repeat;
    }
  }
  
  &-input{
    border: none;
    width: 100%;
    padding: 20px 65px;
    font-weight: 375;
    font-size: 18px;
    line-height: 20px;
  }

  &-texter{
    resize: none;
    padding: 20px 30px;
    font-weight: 375;
    font-size: 18px;
    line-height: 20px;
    border: 1px solid $Bggray;
    height: 240px;
  }
}


//--------------FINISH----------------

.finish{

  display: none;
  max-width: 600px;
  &__img{
    margin-bottom: 40px;
  }

  &__body{
    margin-bottom: 60px;

    .form__items-title{
      margin-bottom: 10px;
    }

    &-text{
      font-weight: 375;
      font-size: 20px;
      line-height: 25px;
      color: $fsgray;
      & + .finish__body-text{
        margin-top: 10px;
      }
    }
  }

  &__btn{
    max-width: 340px;
    padding: 18px 36px 18px 71px;
    background-color: $Bgred;
    color: $fswhite;
    position: relative;
    transition: all .3s;
    &::after{
      content: "";
      position: absolute;
      display: block;
      background: url(../img/finish/google-btn.svg) no-repeat;
      top: 18px;
      left: 37px;
      width: 24px;
      height: 24px;
    }

    &:hover{
      background-color: #851c1c;
    }
  }
}

