*,
*::after,
*::before {
    box-sizing: border-box;
}

a{
  color: inherit;
  text-decoration: none;
}

body {
  // font-family: "Author", sans-serif;
  font-size: 20px; 
  line-height: 22px;
  font-weight: 375;
  color: $fsblue;
  overflow-y: scroll;
}

.container {
  max-width: px;
  padding: 0 10px; 
  margin: 0 auto; 
  height: 100%;
  // overflow-x: hidden;
}

.main{
  margin: 40px 20px 50px 200px;
}

.logo{
  width: 70px;
  img{
    margin-bottom: 30px;
  }
}

.title{
  font-weight: 494;
  font-size: 50px;
  line-height: 56px;
  color: $fsblue;
  margin-bottom: 10px;
}

.text{
  color: $fsgray;
}

.btn{
  color: $fswhite;
  background-color: $Bgred;
  font-size: 22px;
  line-height: 24px;
  transition: all .3s;
  &:hover{
    background-color: #851c1c;
  }
}

.form__items-title{
  margin: 40px 0px 20px;
  font-weight: 494;
  font-size: 25px;
  line-height: 28px;
  color: $fsblue;
}

.next__step{
  border: none;
  padding: 18px 50px;
  cursor: pointer;
}

.back__step{
  margin-right: 20px;
  cursor: pointer;
  color: $fsblue;
  border: 1px solid #E5E4E1;
  background-color: transparent;
  padding: 17px 50px;
  transition: all 0.3s;
  &:hover{
    background-color: #c8c8c8;
  }
}